import React, { Component } from "react";
import { Card, Col } from "react-bootstrap";
import "../Stylesheets/Features.css";

class CardFeature extends Component {
  constructor(props) {
    super(props);

    this.state = {
      title: props.title,
      text: props.text,
    };
  }

  render() {
    return (
      <Col md={6} lg={4} xs={12} className="card-wrap mb-4">
        <Card className="p-3 h-100" style={{ borderRadius: "1rem" }}>
          <Card.Body>
            <Card.Title>{this.state.title}</Card.Title>
            <Card.Text>{this.state.text}</Card.Text>
          </Card.Body>
        </Card>
      </Col>
    );
  }
}

export default CardFeature;
